@import url("https://fonts.googleapis.com/css?family=Open+Sans:300i,400,700|Prata&subset=cyrillic");
h1, h2, h3, h4, h5, h6 {
  font-family: Prata, "Times New Roman", serif; }

p, address {
  font-family: "Open Sans", Helvetica, Arial, sans-serif; }

img {
  max-width: 100%; }

header {
  background: #146aa4;
  padding: 12px 0;
  box-shadow: 0 3px 13px 0 rgba(0, 0, 0, 0.2); }
  header address {
    color: white;
    font-style: italic;
    font-weight: 300;
    font-size: 18px;
    line-height: 24px;
    padding: 0;
    margin: 0; }
  header .phone {
    color: white;
    font-family: Prata, "Times New Roman", serif;
    font-size: 28px; }

#first-screen {
  padding-top: 70px;
  background: url("../img/invisalign.png") no-repeat;
  background-size: contain; }
  #first-screen .first-screen-logo {
    margin-bottom: 35px; }
  #first-screen h1 {
    text-transform: uppercase;
    color: #146aa4;
    font-size: 56px;
    margin-bottom: 30px; }
  #first-screen h2 {
    color: #146aa4;
    font-size: 30px;
    letter-spacing: 1px;
    line-height: 1.45;
    margin-bottom: 40px; }
  #first-screen .desc {
    font-size: 24px; }

footer {
  background: #ecf3f7;
  padding: 55px 0; }
